.services__container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 3rem;
}
.services__container > .service {
  background: var(--color-bg-variant);
  border-radius: 0 0 2rem 2rem;
  border: 1px solid var(--color-primary);
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  transition: var(--transition);
}
.services__container > .service:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}
.services__container > .service > .service__head {
  background: var(--color-primary);
  padding: 2rem;
  border-radius: 0 0 2rem 2rem;
  box-shadow: 0 2rem 1rem rgba(0, 0, 0, 0.1);
}
.services__container > .service > .service__head > h3 {
  color: var(--color-bg-variant);
  font-size: 1rem;
  text-align: center;
}
.services__container > .service > .service__list {
  padding: 2rem;
}
.services__container > .service > .service__list > li {
  display: flex;
  gap: 1rem;
  margin-bottom: 0.8rem;
}
.services__container > .service > .service__list > li > .service__list-icon {
  color: var(--color-primary);
  margin-top: 2px;
}
.services__container > .service > .service__list > li > p {
  font-size: 1rem;
}

/* ================== Media Queries (Medium Devices) ===============*/
@media screen and (max-width: 1024px) {
  .services__container {
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
  }
  .service {
    height: auto;
  }
}
/* ================== Media Queries (Small Devices) ===============*/
@media screen and (max-width: 600px) {
  .services__container {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
}/*# sourceMappingURL=Services.css.map */