.portfolio__container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2.5rem;
}
.portfolio__container > .portfolio__item {
  background: var(--color-bg-variant);
  padding: 1.3rem;
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
}
.portfolio__container > .portfolio__item:hover {
  border-color: var(--color-primary-variant);
  background: transparent;
}
.portfolio__container > .portfolio__item > .portfolio__item-image {
  border-radius: 1.5rem;
  overflow: hidden;
  transition: var(--transition);
}
.portfolio__container > .portfolio__item > .portfolio__item-image:hover {
  border-radius: 0.3rem;
}
.portfolio__container > .portfolio__item > h3 {
  margin: 1.2rem 0 2rem;
}
.portfolio__container > .portfolio__item > .portfolio__item-cta {
  display: flex;
  gap: 1rem;
  margin-bottom: 1pt;
}

/* ================== Media Queries (Medium Devices) ===============*/
@media screen and (max-width: 1024px) {
  .portfolio__container {
    grid-template-columns: 1fr 1fr;
    gap: 1.2rem;
  }
}
/* ================== Media Queries (Small Devices) ===============*/
@media screen and (max-width: 600px) {
  .portfolio__container {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
}/*# sourceMappingURL=Portfolio.css.map */