.container.contact__container {
  width: 58%;
  display: grid;
  grid-template-columns: 30% 58%;
  gap: 12%;
}
.container.contact__container .contact__options {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
.container.contact__container .contact__options > .contact__option {
  background: var(--color-bg-variant);
  padding: 1.2rem;
  border-radius: 1.2rem;
  text-align: center;
  border: 1px solid transparent;
  transition: var(--transition);
}
.container.contact__container .contact__options > .contact__option:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
}
.container.contact__container .contact__options > .contact__option .contact__icon {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}
.container.contact__container .contact__options > .contact__option > a {
  margin-top: 0.7rem;
  display: inline-block;
  font-size: 0.8rem;
}
.container.contact__container > form {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
.container.contact__container > form > input, .container.contact__container > form textarea {
  width: 100%;
  padding: 1.5rem;
  border-radius: 0.5rem;
  background: transparent;
  border: 2px solid var(--color-primary-variant);
  resize: none;
  color: var(--color-white);
}
.container.contact__container > form > button {
  color: black;
}

/* ================== Media Queries (Medium Devices) ===============*/
@media screen and (max-width: 1024px) {
  .container.contact__container {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
}
/* ================== Media Queries (Small Devices) ===============*/
@media screen and (max-width: 600px) {
  .container.contact__container {
    width: var(--container-width-sm);
  }
}/*# sourceMappingURL=Contacts.css.map */