.experience__container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
    >div{
        background: var(--color-bg-variant);
        padding: 2.4rem 5rem;
        border-radius: 2rem;
        border: 1px solid transparent;
        transition: var(--transition);
        &:hover{
            background: transparent;
            border-color: var(--colr-primary-variant);
            cursor: default;
        }
        >h3{
            text-align: center;
            margin-bottom: 2rem;
            color: var(--colr-primary);
        }
    }
    .experience__content{
        display: grid;
        grid-template-columns: 1fr 1fr;
        row-gap: 2rem;
        >.experience__details{
            display: flex;
            gap: 1rem;
            >.icon{
                margin-top: 6px;
                color: var(--colr-primary);
            }
        }
    }
}

/* ================== Media Queries (Medium Devices) ===============*/
@media screen and (max-width: 1024px) {
    .experience__container{
        grid-template-columns: 1fr;
        >div{
            width: 80%;
            padding: 2rem;
            margin: 0 auto;
        }
        .experience__content{
            padding: 1rem;
        }
    }
}

/* ================== Media Queries (Small Devices) ===============*/
@media screen and (max-width: 1024px) {
    .experience__container{
        gap: 1rem;
        >div{
            width: 100%;
            padding: 2rem 1rem;
            margin: 0 auto;
        }
        .experience__content{
            padding: 1rem;
        }
    }
}